body {
    &._loaded {
        .wrapper {
            opacity: 1; } } }
.wrapper {
    opacity: 0;
    transition: opacity .7s; }

.test {
    position: relative;
    width: 500px;
    height: 500px;
    video {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1; }
    #demo {
        position: absolute;
        left: 0;
        bottom: 0;
        z-index: 2;
        height: 10px;
        background-color: #fff;
        color: #000; } }

.timecodes {
    p {
        display: none;
        &._show {
            display: block; } } }

.back-to-top {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: red;
    position: fixed;
    bottom: 50px;
    right: 10px;
    z-index: 10;
    opacity: 0;
    &._show {
        opacity: 1; } }

.js-tabs {
    &__trigger {
        width: 100px;
        background-color: red;
        margin-right: 20px;
        display: inline-block; }
    &__block {
        display: none;
        margin: 20px 0 0 0;
        &._open {
            display: block; } } }

.spoller {
    background-color: red;
    display: inline-block;
    margin: 20px 0; }

.sp-test {
    // display: none
    overflow: hidden;
    transition: all 1s; }

.btn-effect {
    width: 200px;
    height: 50px;
    background-color: red;
    position: relative;
    overflow: hidden;
    span,svg {
        position: relative;
        z-index: 2; }
    i {
        position: absolute;
        display: block;
        width: 0;
        height: 0;
        border-radius: 50%;
        background-color: black;
        transform: translate(-50%,-50%);
        z-index: 1;
        transition: width .7s, height .7s; }
    @media (any-hover: hover) {
        &:hover {
            color: #fff;
            i {
                width: 500px;
                height: 500px; } } } }
